// 
export function uploadImageAsync(_objData,_that) {
	return new Promise( (resolve, reject) => {
		_that.context.post({
			url: url.uploadImage,
			data: _objData,
			//
			success: function(res) {
				if (res.statusCode == 200) {
					let imgInfo = res.data;
					resolve(imgInfo);
				}
			},
			fail: function(e) {
				uni.hideLoading();
				uni.showToast({
					title: "",
					icon: 'none'
				})
			}
		});
	})
}