
import url from '../../constant/url.js'

/**
 * 
 * @param {Object} _that 
 * @param {Object} _data 
 */
export function queryMyResourceStoreInfo(_that,_data){
	return new Promise(function(reslove,reject){
		_that.context.get({
			url: url.queryResourceStoreResName,
			data:_data,
			success: function(res) {
				reslove(res.data);
			},
			fail: function(e) {
				wx.showToast({
					title: "",
					icon: 'none',
					duration: 2000
				})
			}
		})
	});
}

