import url from '../../constant/url.js'
/**
 * 
 * @param {Object} _that 
 * @param {Object} _data 
 */
export function loadApplyRooms(_that,_data){
	return new Promise(function(reslove,reject){
		_that.context.get({
			url: url.listApplyRoom,
			data:_data,
			success: function(res) {
				_that.onoff = true;
				if(res.data.code == 0){
					reslove(res.data);
				}else{
					wx.showToast({
						title: "",
						icon: 'none',
						duration: 2000
					})
				}
			},
			fail: function(e) {
				wx.showToast({
					title: "",
					icon: 'none',
					duration: 2000
				})
			}
		})
	});
}

