import {
	isEmpty
} from './StringUtil'
/**
 * 
 * 1.imgstylewidthheight
 * 2.imgstylemax-width:100%;height:auto
 * 3.stylewidthmax-width:100%
 * 4.<br/>
 * @param html
 * @returns {void|string|*}
 */
export function formatRichText(html) {
	let newContent = html.replace(/<img[^>]*>/gi, function(match, capture) {
		match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '');
		match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '');
		match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '');
		return match;
	});
	newContent = newContent.replace(/style="[^"]+"/gi, function(match, capture) {
		match = match.replace(/width:[^;]+;/gi, 'max-width:100%;').replace(/width:[^;]+;/gi, 'max-width:100%;');
		return match;
	});
	newContent = newContent.replace(/<br[^>]*\/>/gi, '');
	newContent = newContent.replace(/\<img/gi, '<img style="max-width:100%;height:auto;display:block;margin:10px 0;"');
	return newContent;
}
/**
 *  src
 * @param {Object} _content 
 * @param {Object} _url 
 */
export function replaceImgSrc(_content, _url) {
	console.log('_content', typeof(_content))
	if (isEmpty(_content)) {
		return _content;
	}
	_content = _content.replaceAll("&amp;","&")
	// a img
	let b = /<img [^>]*src=['"]([^'"]+)[^>]*>/g; // img src
	let s = _content.match(b); // img  s
	if (!isEmpty(s)) {
		for (let i = 0; i < s.length; i++) {
			let srcImg = s[i].replace(b, '$1'); //src
			if (srcImg.slice(0, 4) == 'http' || srcImg.slice(0, 5) == 'https') {
				//src45httphttps
			} else {
				// imgsrc 
				_content = _content.replace(srcImg, _url + srcImg);
			}
		}
	}

	return formatRichText(_content);
}
