import request from './request.js';
import util from './utils/util.js';
import date from './utils/date.js';
import {hasSession} from './api/Java110SessionApi.js'

import {getHeaders} from './api/SystemApi.js'

export default { 
	getHeaders : getHeaders,
	//
	checkLogin:function(){
		return hasSession();
	},
	/**
	 * 
	 */
	clearAll:function(){
		uni.clearStorage();
	},
	/**
	 * json 
	 * @param {Object} _key 
	 */
	getJson:function(_key){
		try {
			const value = uni.getStorageSync(key);
			if (value) {
				return JSON.parse(value);
			}
		} catch (e) {
			// error
			return null;
		}
	},
	/**
	 * 
	 * @param {Object} key 
	 * @param {Object} value 
	 */
	setData:function(key, value) {
		try {
			uni.setStorageSync(key, value);
		} catch (e) {
			// error
		}
	},
	/**
	 * 
	 * @param {Object} key 
	 */
	getData : function(key) {
		try {
			const value = uni.getStorageSync(key);
			if (value) {
				return value;
			}
		} catch (e) {
			// error
			return null;
		}
	},
	/**
	 * json 
	 * @param {Object} key 
	 * @param {Object} value json 
	 */
	setJson : function(key, value) {
		let jsonString = JSON.stringify(value);
		try {
			uni.setStorageSync(key, jsonString);
		} catch (e) {
			// error
		}
	},
	
	/**
	 * 
	 * @param {Object} _reqObj 
	 */
	request:function(_reqObj){
		if(!_reqObj.hasOwnProperty("header")){
			_reqObj.header = this.getHeaders()
		}
		//
		request.request(_reqObj);
	},
	/**
	 * get 
	 * @param {Object} _reqObj 
	 */
	get:function(_reqObj){
		_reqObj.method = 'GET';
		this.request(_reqObj);	
	},
	/**
	 * post 
	 * @param {Object} _reqObj 
	 */
	post:function(_reqObj){
		_reqObj.method = 'POST';
		this.request(_reqObj);
	},
	/**
	 * 
	 * @param {Object} _urlInfo 
	 */
	navigateTo:function(_urlInfo){
		uni.navigateTo(_urlInfo);
	}
}